import AclGuard from "components/AclGuard/AclGuard";
import ButtonBox from "components/ButtonBox";
import CustomTreeWithCheckBoxes, {
  ICustomTreeWithCheckBoxesRef,
} from "components/CustomTreeWithCheckBoxes";
import ErrorDialog from "components/ErrorDialog";
import InputBox from "components/InputBox";
import PageTitle from "components/PageTitle";
import SimpleDropDown from "components/SimpleDropDown";
import ToastCustom from "components/Toast";
import { RouteProps, withRouter } from "hoc/withRouter";
import { ToastPositionType } from "primereact/toast";
import queryString from "query-string";
import { useEffect, useRef, useState } from "react";
import { linkAccessControl, linkAccessControlCreateViewEdit } from "routes";
import RolesService from "services/AccessControl/RolesService";
import ApprovalRequestService from "services/masterdata/Approval/ApprovalRequestService";
import StaticMasterTypeService from "services/masterdata/StaticMaster/StaticMasterTypeService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  stringifyObject,
  validateGetAPIResponse,
} from "utils/CommonUtil";
import {
  ACTIVE_TYPE,
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  ENTITY,
  INPUT_STYLE_TYPE,
  OPERATION_TYPE,
  PAGE_MODE,
  REVIEW_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  STATUS_LIST,
  TOAST_STYLE_TYPE,
} from "utils/Constant";

function capitalizeWords(inputString: any) {
  return inputString.replace(/\b\w/g, (char: any) => char.toUpperCase());
}

const PageRoleCMScreen = (props: RouteProps) => {
  const [pageObj, setPageObj] = useState({
    name: "",
    displayName: "",
    systemCode: "",
    description: "",
    reasons: "",
    isActive: "",
    savedMenuItems: [],
    userType: "",
  });
  const [initialObj, setInitialObj] = useState<any>({});
  const [pageMode, setPageMode] = useState(PAGE_MODE.CREATE);
  const [uid, setUid] = useState<string | undefined>(undefined);
  const [reviewMode, setReviewMode] = useState<string | undefined>(undefined);
  const [, forceUpdate] = useState<undefined | boolean>(undefined);
  const [displayNameDuplicateError, setDisplayNameDuplicateError] = useState(false);
  const [systemCodeDuplicateError, setSystemCodeDuplicateError] = useState(false);
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [toastMessage, setToastMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [navigateBack, setNavigateBack] = useState(false);
  const toastMessageRef = useRef(null);
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right");
  // from here u will see for checkBoxTreeTable
  const [menuItems, setMenuItems] = useState<any>([]);
  const [mainPayload, setMainPayload] = useState<any>({});
  const [initialPayload, setInitialPayload] = useState<any>([]);
  const [ApiDataForPayload, setApiDataForPayload] = useState([]);
  const { isActive } = pageObj;

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );
  const menuRef = useRef<ICustomTreeWithCheckBoxesRef>(null);

  /*
  -------------------------------------------------------
    Hooks: useEffect
  -------------------------------------------------------
*/

  useEffect(() => {
    fetchTreeTableDataWithCheckboxesData();
    const parsed: {
      mode?: string;
      uid?: string;
      reviewMode?: string;
    } = queryString.parse(props.location.search);
    if (parsed?.mode && parsed?.mode !== undefined) {
      setPageMode(parsed.mode);
    }

    if (parsed?.reviewMode && parsed?.reviewMode !== undefined) {
      setReviewMode(parsed.reviewMode);
    }
    if (parsed?.uid && parsed?.uid !== undefined) {
      setUid(parsed.uid);
      if (parsed?.mode == PAGE_MODE.REVIEW) {
        ApprovalRequestService.getByUid(parsed.uid)
          .then((res: any) => {
            if (validateGetAPIResponse(res)) {
              if (Object.keys(res.data).length > 0 || res.data.id) {
                let jsonObject;
                if (reviewMode == REVIEW_MODE.NEW) {
                  jsonObject = JSON.parse(res.data.finalStateJson);
                } else {
                  jsonObject = JSON.parse(res.data.initialStateJson);
                }
                setPageObj({
                  name: jsonObject?.name ?? "",
                  displayName: jsonObject?.name ?? "",
                  systemCode: jsonObject?.code ?? "",
                  description: jsonObject?.description ?? "",
                  reasons: jsonObject?.reason ?? "",
                  isActive: jsonObject?.isActive ? ACTIVE_TYPE.ACTIVE : ACTIVE_TYPE.INACTIVE,
                  savedMenuItems: jsonObject?.features,
                  userType: jsonObject?.userType,
                });
                setInitialObj({ ...jsonObject });
              }
            }
          })
          .catch((err) => {
            // IF API fails to load, show the error message
          });
      } else {
        RolesService.findById(parsed.uid)
          .then((res: any) => {
            if (validateGetAPIResponse(res)) {
              if (Object.keys(res.data).length > 0 || res.data.id) {
                setApiDataForPayload(res.data.features);
                setPageObj({
                  name: res?.data?.name ?? "",
                  displayName: res?.data?.name ?? "",
                  systemCode: res?.data?.code ?? "",
                  description: res?.data?.description ?? "",
                  reasons: res?.data?.reason ?? "",
                  isActive: res?.data?.isActive ? ACTIVE_TYPE.ACTIVE : ACTIVE_TYPE.INACTIVE,
                  savedMenuItems: res?.data?.features,
                  userType: res?.data?.type,
                });
                setInitialObj({ ...res?.data });
              }
            }
          })
          .catch((err) => {
            // IF API fails to load, show the error message
          });
      }
    }
  }, []);
  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  /* 
  -----------------------------------------------------------------------------------------------------------------------
  Validators:
  -----------------------------------------------------------------------------------------------------------------------
*/

  const validateField = async (e: any) => {
    if (e.target.name === "displayName") {
      let search = `name=${e.target.value}`;
      if (pageMode === PAGE_MODE.EDIT) {
        search = `name=${e.target.value}&uid:nq=${uid}`;
      }
      await StaticMasterTypeService.getAll(0, 10, `${search}&`).then((res: any) => {
        if (res.data?.results && res.data?.results.length > 0) {
          setDisplayNameDuplicateError(true);
        } else {
          setDisplayNameDuplicateError(false);
        }
      });
    }
    if (e.target.name === "systemCode") {
      let search = `code=${e.target.value}`;
      if (pageMode === PAGE_MODE.EDIT) {
        search = `code=${e.target.value}&uid:nq=${uid}`;
      }
      await StaticMasterTypeService.getAll(0, 10, `${search}&`).then((res: any) => {
        if (res.data?.results && res.data?.results.length > 0) {
          setSystemCodeDuplicateError(true);
        } else {
          setSystemCodeDuplicateError(false);
        }
      });
    }
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };
  const validateForm = async () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
  -------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
  -------------------------------------------------------
*/
  const handleInputChange = (event: any) => {
    const { name, value } = event.target;
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      [name]: value,
    }));
  };

  /* 
  -------------------------------------------------------
    ButtonClick Functions:
  -------------------------------------------------------
*/

  const onSaveClick = async () => {
    setErrorMessage("");
    let isValid = false;
    isValid = await validateForm();
    if (!isValid) {
      setPosition("top-right");
      setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
      setTriggerToast(!triggerToast);
      return;
    }
    if (pageMode === PAGE_MODE.CREATE) {
      const enrichedObj = prepareToSave();
      return RolesService.save(enrichedObj)
        .then((res) => {
          setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
          setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
          setTriggerToast(!triggerToast);
          setNavigateBack(true);
        })
        .catch((err) => {
          setErrorMessage(parseStandardAPIErrorMessage(err));
        });
    } else {
      const enrichedObj = await prepareToUpdate();
      console.log(enrichedObj, "this is legend");
      return RolesService.update(enrichedObj, uid)
        .then((res) => {
          setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
          setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
          setTriggerToast(!triggerToast);
          setNavigateBack(true);
        })
        .catch((err) => {
          setErrorMessage(parseStandardAPIErrorMessage(err));
        });
    }
  };

  const onCancelClick = () => {
    props.navigate(linkAccessControl);
  };

  const onApproveClick = async () => {
    setErrorMessage("");
    let isValid = false;
    isValid = await validateForm();
    if (!isValid) {
      return;
    }
    const enrichedObj = await prepareToApprove();

    return StaticMasterTypeService.approve(enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setNavigateBack(true);
      })
      .catch((err) => {
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const onBackClick = () => {
    props.navigate(linkAccessControl);
  };

  const onEditClick = () => {
    setPageMode(PAGE_MODE.EDIT);
    props.navigate(`${linkAccessControlCreateViewEdit}?mode=${PAGE_MODE.EDIT}&uid=${uid}`);
  };

  /* 
  -------------------------------------------------------
    Helper Functions:
  -------------------------------------------------------
*/

  const getPageTitle = () => {
    switch (pageMode) {
      case PAGE_MODE.CREATE:
        return "heading.accessControl.role.create";
      case PAGE_MODE.REVIEW:
        return "heading.accessControl.roles.review";
      case PAGE_MODE.VIEW:
        return "heading.accessControl.roles.view";
      case PAGE_MODE.EDIT:
        return "heading.accessControl.roles.edit";
      default:
        return "heading.accessControl.role.create";
    }
  };

  const prepareToSave = () => {
    const enrichForSave = {
      name: pageObj.name,
      features: menuRef?.current?.prepareDataForAPI(),
    };
    return enrichForSave;
  };
  const updateFeatures = (firstArray: any, secondArray: any) => {
    // debugger
    return firstArray.map((obj1: any) => {
      const matchedFeature = secondArray.find((obj2: any) => obj2.feature.uid === obj1.feature.uid);
      if (matchedFeature) {
        obj1.uid = matchedFeature.uid;
      }
      return obj1;
    });
  };
  const prepareToUpdate = async () => {
    const enrichedObj = {
      ...initialObj,
      name: pageObj.name,
      features: menuRef?.current?.prepareDataForAPI(),
    };
    return enrichedObj;
  };
  const prepareToApprove = async () => {
    let enrichForSave;
    if (pageMode === PAGE_MODE.CREATE) {
      const finalJsonObject = {
        name: pageObj.name,
        features: mainPayload,
      };
      enrichForSave = {
        name: pageObj.name,
        operationType: OPERATION_TYPE.CREATE,
        entity: ENTITY.ACL_ROLE,
        initialStateJson: "",
        finalStateJson: stringifyObject(finalJsonObject),
        reqPayload: stringifyObject(finalJsonObject),
        reasonForChange: "Create",
      };
    } else {
      const res = await RolesService.findById(uid);

      const updatedFirstArray = updateFeatures(mainPayload, res?.data?.features);
      const initialJsonObject = {
        name: initialObj?.name,
        code: initialObj.code,
        reason: initialObj.reason,
        description: initialObj.description,
        createdAt: initialObj.createdAt,
        createdBy: initialObj.createdBy,
        id: initialObj.id,
        isActive: initialObj.isActive,
        requestId: initialObj.requestId,
        uid: initialObj.uid,
        updatedAt: initialObj.updatedAt,
        updatedBy: initialObj.updatedBy,
        features: initialPayload,
      };
      const finalJsonObject = {
        ...initialJsonObject,
        name: pageObj.name,
        features: mainPayload,
      };
      enrichForSave = {
        name: pageObj.name,
        operationType: OPERATION_TYPE.UPDATE,
        entity: ENTITY.ACL_ROLE,
        initialStateJson: stringifyObject(initialJsonObject),
        finalStateJson: stringifyObject(finalJsonObject), //this is what
        reqPayload: stringifyObject(finalJsonObject), //this is what
        reasonForChange: pageObj.reasons, //this is what
      };
    }
    return enrichForSave;
  };

  // this is for the treeTableCheckBox

  const prepareMenuForTreeTableCheckbox = async (menuList: any[]) => {
    let menuItems: any[] = [];

    let objectsWithNullRoot = menuList.filter((obj) => obj.root === null);
    // heres how i find all the  grandparents
    objectsWithNullRoot = objectsWithNullRoot.map((obj) => ({
      ...obj,
      name: capitalizeWords(obj.name), // Capitalize the 'name' property
      // Add other properties if needed
      isSelected: false,
    }));

    menuItems = [...objectsWithNullRoot];

    // now i am gonna find all the parents whose parent is null
    const objectsWithNullParent = menuList.filter(
      (obj) => obj.parent === null && obj.root !== null
    );

    const childrensOfAll = menuList.filter((obj: any) => obj.root !== null && obj.parent !== null);

    const arr3: any = [];

    // Create a mapping of IDs to their corresponding objects in arr1

    // Iterate through arr1 and build the arr3 structure
    objectsWithNullRoot.forEach((item1: any) => {
      const newItem = { ...item1, children: [] };
      const relatedItems = objectsWithNullParent.filter(
        (item2: any) => item2.root && item2.root.id === item1.id
      );

      relatedItems.forEach((relatedItem: any) => {
        if (!relatedItem.parent) {
          newItem.children.push(relatedItem);
        }
      });

      arr3.push(newItem);
    });

    // arr3 it is grandparent and parent interaction

    function appendToArr1(arr1: any, arr2: any) {
      arr2.forEach((item2: any) => {
        const idToAppend = item2.id;
        const parentToFind = item2.parent; // Assuming this is the parent object to attach to

        if (parentToFind) {
          const parentID = parentToFind.id;
          const parentObject = findObjectById(arr1, parentID);

          if (parentObject) {
            if (!parentObject.children) {
              parentObject.children = [];
            }
            parentObject.children.push(item2);
          }
        } else {
          // If there's no parent, find the object by id and append to the root of arr1
          const objectToAppendToRoot = findObjectById(arr1, idToAppend);
          if (objectToAppendToRoot) {
            if (!objectToAppendToRoot.children) {
              objectToAppendToRoot.children = [];
            }
            objectToAppendToRoot.children.push(item2);
          }
        }
      });
    }
    function findObjectById(arr: any, id: any) {
      for (const item of arr) {
        if (item.id === id) {
          return item;
        }
        if (item.children) {
          const childResult: any = findObjectById(item.children, id);
          if (childResult) {
            return childResult;
          }
        }
      }
      return null;
    }
    appendToArr1(arr3, childrensOfAll);

    function addDataToObjects(obj: any) {
      if (Array.isArray(obj)) {
        obj.forEach((item) => {
          addDataToObjects(item);
        });
      } else if (typeof obj === "object") {
        obj.data = { name: obj.name };
        if (obj.children && obj.children.length > 0) {
          addDataToObjects(obj.children);
        }
      }
    }
    const menuItem = addDataToObjects(arr3);

    return arr3;
  };
  const assignKeys = (data: any[], parentIndex: string | number | null = null) => {
    const result: any[] = [];

    for (let index = 0; index < data.length; index++) {
      const key = parentIndex !== null ? `${parentIndex}-${index}` : String(index);

      const item = data[index];

      // Recursively process children if they exist
      if (item.children && Array.isArray(item.children)) {
        item.children = assignKeys(item.children, key);
      }

      // Add the item with the generated key and index to the result array
      const itemWithKey: { key: string; index: number; [key: string]: any } = {
        key,
        index,
        ...item,
      };
      result.push(itemWithKey);
    }

    return result;
  };

  const fetchTreeTableDataWithCheckboxesData = async () => {
    const itemsFromApi = await RolesService.getMenuForTreeTableComponent();
    const itemsAfterPreparation = await prepareMenuForTreeTableCheckbox(itemsFromApi.data.results);
    const dataAfterAssigningKeys = assignKeys(itemsAfterPreparation);
    setMenuItems(dataAfterAssigningKeys);
  };

  return (
    <AclGuard>
      <div className="flex flex-col gap-3">
        <div className="flex w-full justify-between  items-center">
          <PageTitle title={getPageTitle()} />
          <div className="flex ">
            {pageMode === PAGE_MODE.CREATE || pageMode === PAGE_MODE.EDIT ? (
              <div className="flex gap-10">
                <div className="flex gap-5">
                  <ButtonBox buttonType={BUTTON_TYPE.CANCEL_OUTLINE} onClick={onCancelClick} />
                  <ButtonBox
                    buttonType={BUTTON_TYPE.SAVE_OUTLINE}
                    onClickWithLoader={onSaveClick}
                  />
                </div>
                <div>
                  <ButtonBox
                    buttonType={BUTTON_TYPE.SEND_FOR_APPROVAL}
                    onClickWithLoader={onApproveClick}
                  />
                </div>
              </div>
            ) : pageMode === PAGE_MODE.VIEW ? (
              <>
                <div className="mr-5">
                  <ButtonBox buttonType={BUTTON_TYPE.BACK_OUTLINE} onClick={onBackClick} />
                </div>
                <div className="">
                  <ButtonBox buttonType={BUTTON_TYPE.EDIT_OUTLINE} onClick={onEditClick} />
                </div>
              </>
            ) : (
              <div className="h-18"></div>
            )}
          </div>
        </div>
        <AclGuard override={{ read: [PAGE_MODE.VIEW].includes(pageMode) }}>
          <div className="grid grid-cols-4 gap-3">
            <div className="col-span-1">
              <InputBox
                label="label.accessControl.roles.roles"
                name="name"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                value={String(pageObj.name)}
                onChange={handleInputChange}
                onBlur={validateField}
                validation={
                  displayNameDuplicateError
                    ? "Duplicate Entry Found"
                    : simpleValidator?.current?.message("name", pageObj.name, "mandatory|max:100")
                }
                disabled={pageObj.userType === "SYSTEM_DEFINED"}
              />
            </div>

            {pageMode !== PAGE_MODE.CREATE ? (
              <div className="col-span-1">
                <SimpleDropDown
                  disabled
                  label="input.masterData.staticMaster.isActive"
                  name="isActive"
                  variant={DROPDOWN_STYLE_TYPE.SECONDARY}
                  value={isActive}
                  options={STATUS_LIST}
                  onChange={handleInputChange}
                />
              </div>
            ) : (
              <div className="col-span-2" />
            )}
          </div>
          <CustomTreeWithCheckBoxes
            menuItems={menuItems}
            ref={menuRef}
            savedMenuItems={pageObj.savedMenuItems}
          />
        </AclGuard>
        <div className="">
          <ToastCustom
            position={position}
            ref={toastMessageRef}
            toastText={toastMessage}
            variant={toastMessageVariant}
            onRemove={() => {
              if (navigateBack) {
                props.navigate(linkAccessControl);
              }
            }}
          />
        </div>
        <ErrorDialog
          onHide={() => setShowDialogAPIError(false)}
          visible={showDialogAPIError}
          errorMessage={errorMessage}
        />
      </div>

      {/* this is the tree component */}
    </AclGuard>
  );
};
export default withRouter(PageRoleCMScreen);
