import { withAuth } from "components/hoc/withAuth";

const TriStateCheckBox = (props: any) => {
  const { checked, partiallyChecked, isDisabledByAcl, onChange } = props;
  const disabledClassName = isDisabledByAcl
    ? "!bg-[#cecece] !border-primary-gray-300 placeholder-secondary-gray-500 checkbox-disabled"
    : " ";
  const className = `${checked ? "p-highlight" : ""} ${disabledClassName ?? ""}`;
  const icon = partiallyChecked ? "pi pi-minus" : checked ? "pi pi-check" : "";
  const onCheckboxChange = () => {
    if (isDisabledByAcl) return;
    onChange({
      target: {
        checked: !checked,
      },
    });
  };
  return (
    <div
      className="p-checkbox1 p-treetable-checkbox p-component "
      onClick={onCheckboxChange}
      role="checkbox"
      aria-checked={checked}
    >
      <div className="p-hidden-accessible ">
        <input type="checkbox" disabled={true} />
      </div>
      <div className={`p-checkbox-box1 ${className}`}>
        <span className={icon}></span>
      </div>
    </div>
  );
};

export default withAuth(TriStateCheckBox);
