import TriStateCheckBox from "components/TriStateCheckBox/TriStateCheckBox";
import { Column } from "primereact/column";
import { TreeTable } from "primereact/treetable";
import { forwardRef, useEffect, useImperativeHandle, useState } from "react";

const TREE_COLUMN = {
  READ: "Read",
  CREATE: "Create",
  UPDATE: "Update",
  DELETE: "Delete",
  APPROVE: "Approve",
};

type IMode = "Read" | "Create" | "Update" | "Delete" | "Approve";

export interface IMenuItemForAPI {
  read: boolean;
  create: boolean;
  update: boolean;
  delete: boolean;
  approve: boolean;
  feature: {
    uid: string;
  };
  uid?: string;
}
export interface ICustomTreeWithCheckBoxesRef {
  prepareDataForAPI: () => IMenuItemForAPI[];
}

interface ICustomTreeWithCheckBoxesProps {
  menuItems: any[];
  savedMenuItems?: any[];
}

const CustomTreeWithCheckBoxes = forwardRef<
  ICustomTreeWithCheckBoxesRef,
  ICustomTreeWithCheckBoxesProps
>((props, ref) => {
  const { menuItems, savedMenuItems } = props;
  const [menuList, setMenuList] = useState<any[]>([...menuItems]);

  useEffect(() => {
    setMenuList(menuItems);
  }, [menuItems]);

  useEffect(() => {
    if (savedMenuItems && savedMenuItems.length > 0) {
      const updatedTree = updateTreeWithSelectedKeys([...menuList], savedMenuItems);
      console.log("updatedTree", updatedTree);
      setMenuList(updatedTree);
    }
  }, [savedMenuItems]);

  useImperativeHandle(ref, () => ({
    prepareDataForAPI: prepareDataForAPI,
  }));

  const onToggleCheckBox = (event: any, node: any, column: IMode) => {
    const updatedTree = selectNodeByKey([...menuList], node.key, column, event.target.checked);
    setMenuList(updatedTree);
  };

  function selectNodeByKey(tree: any, targetKey: string, mode: IMode, value: boolean) {
    return tree.map((node: any) => {
      if (node.key === targetKey) {
        return {
          ...node,
          [mode]: value,
          [`${mode}_partiallySelected`]: false,
          children: selectAllChildren(node.children, mode, value),
        }; // You can set any property or perform any action for the selected node
      } else if (node.children && node.children.length > 0) {
        const updatedChild = selectNodeByKey(node.children, targetKey, mode, value);
        // this is to get how many children are selected and how many are partially selected
        const selectedChildren = updatedChild.filter(
          (child: any) => child[mode] || child[`${mode}_partiallySelected`]
        );
        const isAllChildrenSelected = updatedChild.every((childNode: any) => childNode[mode]);
        return {
          ...node,
          [mode]: isAllChildrenSelected,
          [`${mode}_partiallySelected`]: !isAllChildrenSelected && selectedChildren.length > 0,
          children: updatedChild,
        };
      }
      return node;
    });
  }

  const selectAllChildren = (children: any, mode: IMode, value: boolean) => {
    if (!children) {
      return [];
    }
    return children.map((child: any) => ({
      ...child,
      [mode]: value,
      children: selectAllChildren(child.children, mode, value),
      [`${mode}_partiallySelected`]: false,
    }));
  };

  const flattenTree = (currentNode: any, flattenedData: IMenuItemForAPI[]) => {
    if (savedMenuItems && savedMenuItems.length > 0) {
      flattenedData.push({
        read: currentNode.Read || currentNode.Read_partiallySelected ? true : false,
        create: currentNode.Create || currentNode.Create_partiallySelected ? true : false,
        update: currentNode.Update || currentNode.Update_partiallySelected ? true : false,
        delete: currentNode.Delete || currentNode.Delete_partiallySelected ? true : false,
        approve: currentNode.Approve || currentNode.Approve_partiallySelected ? true : false,
        feature: {
          uid: currentNode.uid,
        },
        uid: currentNode.savedUid,
      });
    } else {
      flattenedData.push({
        read: currentNode.Read || currentNode.Read_partiallySelected ? true : false,
        create: currentNode.Create || currentNode.Create_partiallySelected ? true : false,
        update: currentNode.Update || currentNode.Update_partiallySelected ? true : false,
        delete: currentNode.Delete || currentNode.Delete_partiallySelected ? true : false,
        approve: currentNode.Approve || currentNode.Approve_partiallySelected ? true : false,
        feature: {
          uid: currentNode.uid,
        },
      });
    }

    if (currentNode.children && currentNode.children.length > 0) {
      currentNode.children.forEach((childNode: any) => flattenTree(childNode, flattenedData));
    }

    return flattenedData;
  };
  const prepareDataForAPI = () => {
    const flattenedData: any = [];
    menuList.forEach((node) => flattenTree(node, flattenedData));
    return flattenedData;
  };

  const updateTreeWithSelectedKeys = (tree: any[], selectedKeys: any[]): any[] => {
    return tree.map((node: any) => {
      const selectedKey = selectedKeys.find((selectedKey) => selectedKey.featureCode === node.code);

      const children = updateTreeWithSelectedKeys(node.children || [], selectedKeys);

      const isAllChildrenSelected_ForReadMode = children.every(
        (child) => child["Read"] || child["Read_partiallySelected"]
      );
      const isAnyChildSelected_ForReadMode = children.some(
        (child) => child["Read"] || child["Read_partiallySelected"]
      );

      const isAllChildrenSelected_ForCreateMode = children.every(
        (child) => child["Create"] || child["Create_partiallySelected"]
      );
      const isAnyChildSelected_ForCreateMode = children.some(
        (child) => child["Create"] || child["Create_partiallySelected"]
      );

      const isAllChildrenSelected_ForUpdateMode = children.every(
        (child) => child["Update"] || child["Update_partiallySelected"]
      );
      const isAnyChildSelected_ForUpdateMode = children.some(
        (child) => child["Update"] || child["Update_partiallySelected"]
      );

      const isAllChildrenSelected_ForDeleteMode = children.every(
        (child) => child["Delete"] || child["Delete_partiallySelected"]
      );
      const isAnyChildSelected_ForDeleteMode = children.some(
        (child) => child["Delete"] || child["Delete_partiallySelected"]
      );

      const isAllChildrenSelected_ForApproveMode = children.every(
        (child) => child["Approve"] || child["Approve_partiallySelected"]
      );
      const isAnyChildSelected_ForApproveMode = children.some(
        (child) => child["Approve"] || child["Approve_partiallySelected"]
      );

      return {
        ...node,
        savedUid: selectedKey?.uid,
        Read: isAllChildrenSelected_ForReadMode ? selectedKey?.read : false,
        [`Read_partiallySelected`]:
          isAnyChildSelected_ForReadMode && !isAllChildrenSelected_ForReadMode,

        Create: isAllChildrenSelected_ForCreateMode ? selectedKey?.create : false,
        [`Create_partiallySelected`]:
          isAnyChildSelected_ForCreateMode && !isAllChildrenSelected_ForCreateMode,
        Update: isAllChildrenSelected_ForUpdateMode ? selectedKey?.update : false,
        [`Update_partiallySelected`]:
          isAnyChildSelected_ForUpdateMode && !isAllChildrenSelected_ForUpdateMode,
        Delete: isAllChildrenSelected_ForDeleteMode ? selectedKey?.delete : false,
        [`Delete_partiallySelected`]:
          isAnyChildSelected_ForDeleteMode && !isAllChildrenSelected_ForDeleteMode,
        Approve: isAllChildrenSelected_ForApproveMode ? selectedKey?.approve : false,
        [`Approve_partiallySelected`]:
          isAnyChildSelected_ForApproveMode && !isAllChildrenSelected_ForApproveMode,
        children: children,
      };
    });
  };
  // console.log("menuList", menuList);

  return (
    <div className="cardOfCheckBox">
      {menuList ? (
        <div className=" p-customTreeTableCheckBox">
          <TreeTable value={menuList} tableStyle={{ minWidth: "50rem" }}>
            <Column field="name" header="Name" className=" p-customTreeTable" expander></Column>
            <Column
              header={TREE_COLUMN.READ}
              body={(node) => (
                <TriStateCheckBox
                  partiallyChecked={node["Read_partiallySelected"]}
                  key={node.key}
                  checked={node["Read"]}
                  onChange={(e: any) => onToggleCheckBox(e, node, "Read")}
                />
              )}
            />
            <Column
              header={TREE_COLUMN.CREATE}
              body={(node) => (
                <TriStateCheckBox
                  partiallyChecked={node["Create_partiallySelected"]}
                  key={node.key}
                  checked={node["Create"]}
                  onChange={(e: any) => onToggleCheckBox(e, node, "Create")}
                />
              )}
            />
            <Column
              header={TREE_COLUMN.UPDATE}
              body={(node) => (
                <TriStateCheckBox
                  partiallyChecked={node["Update_partiallySelected"]}
                  key={node.key}
                  checked={node["Update"]}
                  onChange={(e: any) => onToggleCheckBox(e, node, "Update")}
                />
              )}
            />
            <Column
              header={TREE_COLUMN.DELETE}
              body={(node) => (
                <TriStateCheckBox
                  partiallyChecked={node[`Delete_partiallySelected`]}
                  key={node.key}
                  checked={node["Delete"]}
                  onChange={(e: any) => onToggleCheckBox(e, node, "Delete")}
                />
              )}
            />
            <Column
              header={TREE_COLUMN.APPROVE}
              body={(node) => (
                <TriStateCheckBox
                  partiallyChecked={node["Approve_partiallySelected"]}
                  key={node.key}
                  checked={node["Approve"]}
                  onChange={(e: any) => onToggleCheckBox(e, node, "Approve")}
                />
              )}
            />
          </TreeTable>
        </div>
      ) : (
        <>Loading...</>
      )}
    </div>
  );
});

export default CustomTreeWithCheckBoxes;
